import { Component, OnInit } from '@angular/core';
import { ToastrService } from 'ngx-toastr';
import { IApp } from 'src/app/models/model.user';
import { UserService } from 'src/app/services/user.service';
import { AESEncryptDecryptService } from 'bd-common-lib';

@Component({
  selector: 'app-nav',
  templateUrl: './nav.component.html',
  styleUrls: ['./nav.component.css']
})
export class NavComponent implements OnInit {
  appList: IApp[] = [];
  constructor(
    private userService: UserService,
    public toastr: ToastrService,
    public AES: AESEncryptDecryptService
  ) { }

  ngOnInit() {
    const employeeId = this.AES.decryptionAES(sessionStorage.getItem('empCode')) || '';
    this.userService.getAppList(employeeId)
      .subscribe({
        next: (response) => {
          this.appList = response.applicationList;
        },
        error: (errResponse) => {
          this.toastr.error('Failed to application list. Please try again');
        }
      });
  }

  clickOnAppLists(event: any, app: IApp) {
    this.userService.updateAppSelection(app);
    console.log("employeeId--");
  }
}
