import { CommonModule } from '@angular/common';
import { APP_INITIALIZER, NgModule } from '@angular/core';
import { ReactiveFormsModule } from '@angular/forms';
import { BrowserModule } from '@angular/platform-browser';
import { HTTP_INTERCEPTORS, HttpClientModule } from '@angular/common/http';
import { BDCommonLibModule } from 'bd-common-lib';
import { AppRoutingModule } from './app-routing.module';
import { AppComponent } from './app.component';
import { LoggedOutComponent } from './shared/layout/logged-out/logged-out.component';
import { PageNotFoundComponent } from './shared/layout/page-not-found/page-not-found.component';
import { UnAuthorizedAccessComponent } from './shared/layout/un-authorized-access/un-authorized-access.component';
import { NavComponent } from './shared/layout/nav/nav.component';
import { HomeComponent } from './components/home/home.component';
import { AppConfigService } from './shared/services/app-config.service';
import { FormatDatePipe } from './shared/pipes/format-date.pipe';
import { LoaderComponent } from './shared/components/loader/loader.component';
import { NgxLoadingModule } from 'ngx-loading';
import { APIInterceptor } from './shared/http-interceptor';

@NgModule({
  declarations: [
    AppComponent,
    NavComponent,
    PageNotFoundComponent,
    UnAuthorizedAccessComponent,
    LoggedOutComponent,
    HomeComponent,
    FormatDatePipe,
    LoaderComponent
  ],
  imports: [
    BrowserModule,
    CommonModule,
    AppRoutingModule,
    BDCommonLibModule,
    ReactiveFormsModule,
    HttpClientModule,
    NgxLoadingModule.forRoot({})
  ],
  providers: [{
    provide: APP_INITIALIZER,
    useFactory: (appConfigService: AppConfigService) => {
      return () => {
        return appConfigService.loadConfig();
      }
    },
    multi:true,
    deps: [AppConfigService]
  },
  {
    provide: HTTP_INTERCEPTORS,
    useClass: APIInterceptor,
    multi: true
  },
],
  entryComponents: [LoaderComponent],
  bootstrap: [AppComponent]
})
export class AppModule { }
