import { Injectable } from "@angular/core";
import { tap } from "rxjs/operators";
import {
  HttpRequest,
  HttpHandler,
  HttpEvent,
  HttpInterceptor,
  HttpResponse,
} from "@angular/common/http";
import { Observable } from "rxjs";
import { LoaderService } from "./components/loader.service";
import { NotificationAlertService } from "bd-common-lib";
import { GlobalVar } from "./global-var";

@Injectable()
export class APIInterceptor implements HttpInterceptor {
  constructor(public loaderService: LoaderService, private notify: NotificationAlertService) { }
  intercept(
    request: HttpRequest<any>,
    next: HttpHandler
  ): Observable<HttpEvent<any>> {
    //how to update the request Parameters
    this.loaderService.show();
    //console.log("Before making api call : ");

    if(GlobalVar.Environment.toString().trim() != 'Localhost')
    {
      request = request.clone({
        setHeaders: { Authorization: `Bearer ` + sessionStorage.getItem('authToken')}
      });
    }

    return next.handle(request).pipe(
      tap(
        event => {

          //logging the http response to browser's console in case of a success
          if (event instanceof HttpResponse) {
            this.loaderService.hide();
           // console.log("api call success :", event);
          }
        },
        (error) => {
          this.loaderService.hide();
          //this.notify.showError(error?.error?.error, 'toast-bottom-right');
          if (event instanceof HttpResponse) {
            //console.log("api call error :", event);
          }
        }
      )
    );
  }
}
