import { Pipe, PipeTransform } from '@angular/core';
import * as moment from 'moment';
@Pipe({
  name: 'formatDate'
})
export class FormatDatePipe implements PipeTransform {

  transform(date: Date): unknown {
    if(!date) {
      return '';
    }
    return moment(date).format('DD-MM-YYYY');
  }

}
