import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { AppConfigService } from '../shared/services/app-config.service';
import { map } from 'rxjs/operators';
import { IShowRole, IValidateEmpPayload, IManageRole, IGetAppName, IApp } from '../models/model.user';
import { Subject } from 'rxjs';
import { GlobalVar } from '../shared/global-var';

@Injectable({
  providedIn: 'root'
})
export class UserService {
  private appConfig;
  appSelectedSubject = new Subject();
  constructor(
    private httpClient: HttpClient,
    private appConfigService: AppConfigService
  ) {
    this.appConfig = this.appConfigService.appConfig;
  }

  /**
   * Update the selected app subject, it will be called from nav component
   * @param app selected app
   */
  updateAppSelection(app: IApp) {
    this.appSelectedSubject.next(app);
  }

  /**
   * Make REST call to back end with the employee payload
   * @param payload Validate employee payload
   * @returns a response of employee object
   */
  validateEmp(payload: IValidateEmpPayload) {
    const serverUrl = `${GlobalVar.BASE_API_URL}/user/validateEmp`;
    return this.httpClient.post(serverUrl, payload).pipe(
      map((response: any) => {
        return response.result;
      })
    );
  }

  /**
   * Makes REST call to get list of application id for the given employee id
   * @param payload employee id 
   * @returns a response with list of application list
   */
  getAppList(employeeId: string) {
    const payload = {
      pcemplcode: employeeId
    };
    console.log("Payload-",payload)
    const serverUrl = `${GlobalVar.BASE_API_URL}/user/getMenu`;
    return this.httpClient.post(serverUrl, payload).pipe(
      map((response: any) => {
        return response.result;
      })
    );
  }

  /**
   * Makes REST call to get list of roles
   * @param payload Show role payload
   * @returns Response with role list
   */
  showRole(payload: IShowRole) {
    const serverUrl = `${GlobalVar.BASE_API_URL}/user/showRole`;
    return this.httpClient.post(serverUrl, payload).pipe(
      map((response: any) => {
        return [...response.result.roleResponseList];
      })
    );
  }

  /**
   * Makes REST call to set role
   * @param payload Manage role payload
   * @returns Response with updated role
   */
  manageRole(payload: IManageRole) {
    const serverUrl = `${GlobalVar.BASE_API_URL}/user/manageRole`;
    return this.httpClient.post(serverUrl, payload).pipe(
      map((response: any) => {
        return response.result;
      })
    );
  }

  getAppName(payload: IGetAppName) {
    const serverUrl = `${GlobalVar.BASE_API_URL}/user/getAppName`;
    return this.httpClient.post(serverUrl, payload).pipe(
      map((response: any) => {
        return response.result;
      })
    );
  }
  
  /**
   * Get role based drop down by application id
   * @param payload String
   * @returns roleDropdownList
   */
  getRoleDropDown(payload: string) {
    const serverUrl = `${GlobalVar.BASE_API_URL}/user/getRoleDropdown?appId=${payload}`;
    return this.httpClient.get(serverUrl).pipe(
      map((response: any) => {
        return [...response.result.roleDropdownList];
      })
    );
  }
}
